html {
  scroll-behavior: smooth;
}

body {
  font-family: $font-primary;
  box-sizing: border-box;
}

img {
  width: 100%;
  height: auto;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

a {
  display: flex;

  width: 100%;
  height: 100%;

  text-decoration: none;

  color: inherit;

  justify-content: center;
  align-items: center;
}

.body-container {
  display: flex;
  flex-direction: column;

  align-items: center;
  color: $main-black;

  margin: 0 auto;
}

.verticals-paddings {
  padding: 120px 0;
}

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}


@media (max-width: $tablet-only) {
  .verticals-paddings {
    padding: 70px 0;
  }

  .container {
    max-width: 375px;
  }
}

